<template>
  <div class="container">

    <transition name="fade" appear mode="out-in">
      <component :is="screens[position]" :question="question" :result="result" @goto="handleGoTo"
        @question="handleQuestion" @showResult="showResult" @startOver="startOver" />
    </transition>

  </div>
</template>

<script>

import appConfirmMsg from './components/confirmMsg.vue';
import appInitialMsg from './components/initialMsg.vue';
import appResultMsg from './components/resultMsg.vue';

export default {
  components: {
    appInitialMsg,
    appConfirmMsg,
    appResultMsg
  },
  data() {
    return {
      list: ['Yes', 'No', 'Not sure...', 'Ask a friend!'],
      screens: ['appInitialMsg', 'appConfirmMsg', 'appResultMsg'],
      position: 0,
      question: '',
      result: ''
    }
  },
  methods: {
    handleGoTo(position) {
      this.position = position
    },
    handleQuestion(question) {
      this.question = question
    },
    getRandomValue() {
      return this.list[Math.floor(Math.random() * this.list.length)];
    },
    generateResult() {
      let rand = this.getRandomValue();
      if(this.result !== '') {
        while(rand === this.result) {
          rand = this.getRandomValue();
        }
      }
      this.result = rand;
    },
    showResult() {
      this.generateResult();
    },
    startOver() {
      this.position = 0;
      this.question = '';
      this.result = '';
    }
  }

}

</script>

<style>
@import './assets/style.css';

.fade-enter-from {
  opacity: 0;
}

.fade-enter-active {
  transition: 0.5s;
}

.fade-enter-to {
  opacity: 1;
}


.fade-leave-from {
  opacity: 1;
}

.fade-leave-active {
  transition: 0.5s;
}

.fade-leave-to {
  opacity: 0;
}
</style>
